/* Swiper */
.swiper-pagination-bullets {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  gap: 27px;
}

.swiper-pagination-bullet {
  position: relative;
  width: 15px;
  height: 15px;
  background-color: #ffffff;
  border: 3px solid #189b80;
  border-radius: 50%;
  box-sizing: border-box;
  transition: 0.3s ease;
}
.swiper-pagination-bullet:only-child {
  visibility: hidden;
}

.swiper-pagination-bullet:not(.swiper-pagination-bullet-active) {
  cursor: pointer;
}
.swiper-pagination-bullet:not(.swiper-pagination-bullet-active):hover {
  background-color: rgba(255, 255, 255, 0.6);
}

.swiper-pagination-bullet-active {
  background-color: transparent;
  border-color: #ed344a;
  cursor: default;
}

.swiper-pagination-fill .swiper-pagination-bullet {
  border-color: #ffffff;
}

/* Blocks */
.button {
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: space-between;
  gap: 10px;
  padding: 17px;
  background-color: #189b80;
  color: #ffffff;
  font-family: "Lato", sans-serif;
  font-weight: 700;
  font-size: 15px;
  line-height: 1.2;
  cursor: pointer;
  transition: 0.3s ease;
}
.button:hover:not(.button--angle-hover) {
  background-color: rgba(24, 155, 128, 0.8);
}
.button svg {
  color: #ffffff;
}
.button--angle-hover::after {
  content: "";
  position: absolute;
  top: -20px;
  right: -20px;
  display: block;
  width: 20px;
  height: 20px;
  background-color: inherit;
  transition: 0.3s ease;
}
.button--angle-hover:hover::after {
  transform: translate(-20px, 20px);
}
.button--red {
  background-color: #ed344a;
}
.button--red:hover:not(.button--angle-hover) {
  background-color: rgba(237, 52, 74, 0.8);
}
.button--outline {
  border: 1px solid #ffffff;
}

