}
.news-card__text {
  font-weight: 600;
  font-size: 16px;
  line-height: 1.25;
  margin-top: 10px;
  margin-bottom: 28px;
  display: -webkit-box;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}
.news-card__date {
  color: #8f8f8f;
  font-size: 16px;
  line-height: 1.25;
  margin-top: auto;
}

.news {
  position: relative;
  padding-bottom: 128px;
}
.news::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 40px;
  height: 40px;
  background-color: #ed344a;
}
.news__outer {
  position: relative;
  padding: 56px 0 86px;
  background-color: #575a64;
}
.news__container {
  max-width: 1040px;
}
.news__grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 40px 0;
  margin-top: 45px;
}
@media only screen and (max-width: 991px) {
  .news__grid {
    grid-template-columns: repeat(1, 1fr);
  }
}
.news__more {
  max-width: 328px;
  width: 100%;
  margin-top: 30px;
  position: absolute;
  bottom: -22px;
  left: 50%;
  transform: translateX(-50%);
}